import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

import { PonyModel } from '../shared/models/pony.model';

@Component({
  selector: 'pr-pony',
  templateUrl: './pony.component.html',
  styleUrls: ['./pony.component.css']
})
export class PonyComponent implements OnInit {
  name: string;
  color: string;

  @Input() ponyModel: PonyModel;
  @Input() isRunning: boolean; // To reflect if the pony is running or not
  @Input() isPending: boolean; // To reflect if the pony is pending
  @Input() isWinning: boolean; // To reflect if the pony is winning

  @Output() ponyClicked = new EventEmitter();
  // @Output('ponyClicked') emitter = new EventEmitter();
  // @Output('ponyAlerted') emitterAlert = new EventEmitter();

  constructor() {
  }

  ngOnInit() {
    this.name = this.ponyModel.name;
    this.color = this.ponyModel.color;
  }

  // Get current image url
  getPonyImageUrl() {
    return `assets/img/ponies/pony-${this.ponyModel.color.toLowerCase()}${ this.isRunning ? '-running' : ''}.gif`;
  }

  // Click on Pony
  selectThisPony(e) {
    e.preventDefault();
    this.ponyClicked.emit(this.ponyModel);
  }

}



