import {Component, OnInit, Input, Output, EventEmitter } from '@angular/core';


// Interface RaceModel
import { RaceModel } from '../shared/models/race.model';

// Interface PonyModel
import { PonyModel } from '../shared/models/pony.model';


@Component({
  selector: 'pr-race',
  templateUrl: './race.component.html',
  styleUrls: ['./race.component.css']
})
export class RaceComponent implements OnInit {
  race: RaceModel;
  ponies: Array<PonyModel>;

  @Input() raceModel: RaceModel;
  @Output() raceClicked = new EventEmitter();

  constructor() {}

  ngOnInit() {
    this.race = this.raceModel;
    this.ponies = this.raceModel.ponies;

  }

  selectThisRace() {
    this.raceClicked.emit(this.race);
  }
}



