/**
 * Race service
 */

import { Injectable } from '@angular/core';

// Services
import { HttpService } from './http.service';
import { WsService } from './ws.service';


// Work with an Observable and RxJS
import { Observable } from 'rxjs/Observable';
// import 'rxjs/add/observable/of';
import 'rxjs/add/observable/interval';
import 'rxjs/add/operator/take';
import 'rxjs/add/operator/takeWhile';
import 'rxjs/add/operator/map';
// import 'rxjs/add/operator/catch';

// Components
import { RaceModel } from '../models/race.model';
import { PonyWithPositionModel } from '../models/pony.model';

@Injectable()
export class RaceService {

  // Inject Http service
  constructor(private http: HttpService, private wsService: WsService) { }

  // API REST Http
  list(): Observable<Array<RaceModel>> {
    // return this.http.get('/api/races?status=PENDING')
    // ...and calling .json() on the response to return data
    // .map((res) => res.json())
    // ...errors if any
    // .catch((error: any) => Observable.throw(error.json().error || 'Server error'));

    return this.http.get('/api/races?status=PENDING');
  }

  // Bet on pony
  bet(raceId, ponyId): Observable<RaceModel> {
    // console.log(raceId, ponyId);
    return this.http.post(`/api/races/${raceId}/bets`, {ponyId});
  }

  // Get the detail of a race by its ID
  get(id): Observable<RaceModel> {
    return this.http.get(`/api/races/${id}`);
  }

  // Cancel "Bet on pony"
  cancelBet(raceId): Observable<RaceModel> {
    return this.http.delete(`/api/races/${raceId}/bets`);
  }

  // Get Array of PonyWithPositionModel
  live(raceId): Observable<Array<PonyWithPositionModel>> {
    // return Observable
    //   .interval(1000)
    //   .take(101)
    //   .map(position => {
    //     return [{
    //       id: 1,
    //       name: 'Superb Runner',
    //       color: 'BLUE',
    //       position
    //     }, {
    //       id: 2,
    //       name: 'Awesome Fridge',
    //       color: 'GREEN',
    //       position
    //     }, {
    //       id: 3,
    //       name: 'Great Bottle',
    //       color: 'ORANGE',
    //       position
    //     }, {
    //       id: 4,
    //       name: 'Little Flower',
    //       color: 'YELLOW',
    //       position
    //     }, {
    //       id: 5,
    //       name: 'Nice Rock',
    //       color: 'PURPLE',
    //       position
    //     }];
    //   });

    return this.wsService.connect(`/race/${raceId}`)
      .takeWhile(liveRace => liveRace.status !== 'FINISHED')
      .map(liveRace => liveRace.ponies);
  }


}



