import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';

import { UserService } from '../shared/providers/user.service';

@Component({
  selector: 'pr-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  // Declare FormControl
  loginCtrl: FormControl;
  passwordCtrl: FormControl;
  confirmPasswordCtrl: FormControl;
  birthYearCtrl: FormControl;

  // Declare FormGroup
  userForm: FormGroup; // ngForm
  passwordForm: FormGroup;

  // Error POST : user register
  registrationFailed: boolean;

  // Password FormGroup validator
  static passwordMatch(group: FormGroup) {
    const password = group.get('password').value;
    const confirmPassword = group.get('confirmPassword').value;

    return (password === confirmPassword) ? null : { matchingError: true };
  }

  // Birth date FormControl validator
  static validYear(control: FormControl) {
    const birthYear = control.value;
    return Number.isNaN(birthYear) ||
    birthYear < 1900 ||
    birthYear > new Date().getFullYear() ? { invalidYear: true } : null;
  }

  constructor(fb: FormBuilder, private userService: UserService, private router: Router) {
    // Init fields FormControl
    this.loginCtrl = fb.control('', [Validators.required, Validators.minLength(3)]);
    this.passwordCtrl = fb.control('', Validators.required);
    this.confirmPasswordCtrl = fb.control('', Validators.required);
    this.birthYearCtrl = fb.control('', [Validators.required, RegisterComponent.validYear]);

    // Init passwordForm FormGroup
    this.passwordForm = fb.group(
      {
        password: this.passwordCtrl,
        confirmPassword: this.confirmPasswordCtrl
      },
      { validator: RegisterComponent.passwordMatch }
    );

    // Init userForm FormGroup
    this.userForm = fb.group({
      login: this.loginCtrl,
      passwordForm: this.passwordForm,
      birthYear: this.birthYearCtrl
    });

  }


  ngOnInit() {}

  // Register
  register() {
    this.userService.register(
      this.userForm.value.login,
      this.userForm.value.passwordForm.password,
      this.userForm.value.birthYear
    ).subscribe(
      () => this.router.navigate(['/']),
      () => this.registrationFailed = true
    );
  }
}



