import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

// Scroll page module
import { Ng2PageScrollModule } from 'ng2-page-scroll';

// Routes
import { RouterModule } from '@angular/router';
import { ROUTES } from './app-routing.module';

// Hash URL : /foo => become /#/foo
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

// Components
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { RacesComponent } from './races/races.component';
import { RaceComponent } from './race/race.component';
import { BetComponent } from './bet/bet.component';
import { LiveComponent } from './live/live.component';
import { PonyComponent } from './pony/pony.component';
import { AccountComponent } from './account/account.component';
import { HelpComponent } from './help/help.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';

// Pipes
import { FromNowPipe } from './shared/pipes/from-now.pipe'; // yarn add --exact moment

// Custom Services
import { HttpService } from './shared/providers/http.service';
import { UserService } from './shared/providers/user.service';
import { LoginRouteGuardService } from './shared/providers/login-route-guard.service';

import { RaceService } from './shared/providers/race.service';
import { WsService } from './shared/providers/ws.service';
import * as Webstomp from 'webstomp-client'; // yarn add --exact webstomp-client

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    RacesComponent,
    RaceComponent,
    BetComponent,
    LiveComponent,
    PonyComponent,
    FromNowPipe,
    AccountComponent,
    HelpComponent,
    PageNotFoundComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    RouterModule.forRoot(ROUTES), // Import ROUTES,
    Ng2PageScrollModule.forRoot()


  ],
  providers: [
    HttpService,
    UserService,
    LoginRouteGuardService,
    RaceService,
    WsService,
    { provide: 'WebSocket', useValue: WebSocket },
    { provide: 'Webstomp', useValue: Webstomp },
    { provide: LocationStrategy, useClass: HashLocationStrategy }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



