import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


// Interface RaceModel
import { RaceModel } from '../shared/models/race.model';

// Service provider RaceService
import { RaceService } from '../shared/providers/race.service';

@Component({
  selector: 'pr-races',
  templateUrl: './races.component.html',
  styleUrls: ['./races.component.css']
})
export class RacesComponent implements OnInit {
  races: Array<RaceModel> = [];

  // Injection RaceService
  constructor(private raceService: RaceService, private router: Router) { }

  ngOnInit() {
    // Use method list() from RaceService
    // this.races = this.raceService.list(); // [{name: 'Lyon'},{name: 'London'}]

    // Subscribe to an Observable
    // this.raceService.list().subscribe(
    //   races => {
    //     this.races = races;
    //   },
    //   error => console.log(error),
    //   () => console.log('Done')
    // );

    this.raceService.list().subscribe(races => this.races = races);
  }


  betOnRace(race) {
    this.router.navigate([`/races/${race.id}`]);
    // this.router.navigate([`/account`]);
    // alert(`/races/${race.id}`);
  }
}



