import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { RaceService } from '../shared/providers/race.service';
import { RaceModel } from '../shared/models/race.model';

@Component({
  selector: 'pr-bet',
  templateUrl: './bet.component.html',
  styleUrls: ['./bet.component.css']
})
export class BetComponent implements OnInit {

  race: RaceModel;
  raceId: number;
  betFailed = false;

  constructor(private raceService: RaceService, private route: ActivatedRoute) {
  }

  ngOnInit() {
    const raceId = this.route.snapshot.params['raceId'];
    this.raceService.get(raceId)
      .subscribe(raceBet => { this.race = raceBet; this.raceId = raceId; });

  }

  betOnPony(pony) {
    if (this.isPonySelected(pony)) {
      this.raceService.cancelBet(this.race.id).subscribe(
        () => this.race.betPonyId = null,
        () => this.betFailed = true,
        // () => console.log('Cancel the bet OK!')
      );
    } else {
      this.raceService.bet(this.race.id, pony.id)
        .subscribe(
          raceBet => this.race = raceBet,
          () => this.betFailed = true
        );
    }
  }

  isPonySelected(pony) {
    return pony.id === this.race.betPonyId;
  }

}



