/**
 * API REST : HTTP service
 */
import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';

// Work with an Observable
import { Observable } from 'rxjs/Observable';

@Injectable()
export class HttpService {
  baseUrl: string = 'http://ponyracer.ninja-squad.com';
  headers: Headers = new Headers();
  options: RequestOptions = new RequestOptions({ headers: this.headers });

  constructor(private http: Http) {}

  get(path: string): Observable<any> {
    this.addJwtTokenIfExists();
    return this.http.get(`${this.baseUrl}${path}`, this.options).map(res => res.json());
  }

  post(path: string, body: any): Observable<any> {
    this.addJwtTokenIfExists();
    return this.http.post(`${this.baseUrl}${path}`, body, this.options).map(res => res.json());
  }

  // Delete path if calceling the bet
  delete(path: string): Observable<any> {
    this.addJwtTokenIfExists();
    return this.http.delete(`${this.baseUrl}${path}`, this.options);
  }

  addJwtTokenIfExists() {
    const value = window.localStorage.getItem('rememberMe');
    if (value) {
      const token = JSON.parse(value).token; // user.token
      this.headers.set('Authorization', `Bearer ${token}`);
    } else {
      this.headers.delete('Authorization');
    }
  }

}



