/**
 * Webstomp + Websocket service
 */
import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class WsService {

  constructor(@Inject('WebSocket') private WebSocket, @Inject('Webstomp') private Webstomp) {}

  connect(channel): Observable<any> {
    return Observable.create(
      observer => {

        // TODO create the WebSocket connection
        const connection = new this.WebSocket('ws://ponyracer.ninja-squad.com/ws');

        // TODO create the stomp client with Webstomp
        const stompClient = this.Webstomp.over(connection);

        // TODO connect the stomp client
        let subscription;
        stompClient.connect(
          { login: null, passcode: null },
          () => {
            // TODO subscribe to the specific channel and store the subscription
            subscription = stompClient.subscribe(channel, message => {
              // TODO emit the message received, after extracting the JSON from the body
              const bodyAsJson = JSON.parse(message.body);
              observer.next(bodyAsJson);
            });
          },
          error => {
            // propagate the error
            observer.error(error);
          }
        );

        // TODO handle the unsubscription
        return () => {
          if (subscription) {
            subscription.unsubscribe();
          }
          connection.close();
        };

      });
  }
}



