/**
 * canActivate : true => the user can activate the route "/login"
 */
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { UserService } from './user.service';

@Injectable()
export class LoginRouteGuardService implements CanActivate {

  constructor(private userService: UserService, private router: Router) {}


  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if (this.userService.isLoggedOut()) {
      // User logged out so return true
      return true;
    }

    // User logged in so redirect to home page with the return url and return false
    this.router.navigate(['/home'], { queryParams: { returnUrl: state.url } });
    return false;
  }
}



