import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { RaceService } from '../shared/providers/race.service';
import { RaceModel } from '../shared/models/race.model';
import { PonyWithPositionModel } from '../shared/models/pony.model';

import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'pr-live',
  templateUrl: './live.component.html',
  styleUrls: ['./live.component.css']
})
export class LiveComponent implements OnInit, OnDestroy {
  race: RaceModel;
  error: boolean;
  betWon: boolean;

  // Array of ponies with position
  poniesWithPosition: Array<PonyWithPositionModel> = [];

  // Storage of race winners
  winners: Array<PonyWithPositionModel>;

  positionSubscription: Subscription;

  constructor(private raceService: RaceService, private route: ActivatedRoute) { }

  ngOnInit() {
    const raceId = this.route.snapshot.params['raceId'];

    // this.positionSubscription = this.raceService.live(raceId).subscribe(res => this.poniesWithPosition = res);
    this.positionSubscription = this.raceService.get(raceId)
      .do(race => this.race = race)
      .filter(() => this.race.status !== 'FINISHED')
      .switchMap(race => this.raceService.live(race.id))
      .subscribe(
        poniesLiveRace => {
          this.poniesWithPosition = poniesLiveRace;
          this.race.status = 'RUNNING';
        },
        error => this.error = true,
        () => {
          if (this.poniesWithPosition !== undefined) {
            this.race.status = 'FINISHED';
            this.winners = this.poniesWithPosition.filter(pony => pony.position >= 100);
            this.betWon = this.winners.some(value => value.id === this.race.betPonyId);
          }
        }
      );
  }

  ngOnDestroy() {
    if (this.positionSubscription) {
      this.positionSubscription.unsubscribe();
    }
  }
}



