/**
 * Router configuration
 * Installs Router routes, components and services
 */

import { Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { RacesComponent } from './races/races.component';
import { BetComponent } from './bet/bet.component';
import { LiveComponent } from './live/live.component';
import { AccountComponent } from './account/account.component';

import { PageNotFoundComponent } from './page-not-found/page-not-found.component';

import { LoginRouteGuardService } from './shared/providers/login-route-guard.service';

/**
 * Default route would be also: { path: '', redirectTo: '/path', pathMatch: 'full' }
 * Don't do pathMatch: 'prefix' because every URL would match ''
 *
 * canActivate : true => the user can activate the route
 */
export const ROUTES: Routes = [ // Array of routes passed to RouterModule.forRoot()
  // { path: '', component: HomeComponent }, // Default route
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'login', component: LoginComponent, canActivate: [LoginRouteGuardService] },
  { path: 'account', component: AccountComponent },
  { path: 'races/:raceId', component: BetComponent },
  {
    path: 'races',
    children: [
      { path: '', component: RacesComponent },
      { path: ':raceId', component: BetComponent },
      { path: ':raceId/live', component: LiveComponent }
    ]
  },
  { path: '**', component: PageNotFoundComponent } // Wildcard route
];



